import { mapState } from 'vuex'
export default {
	data() {
		return {
			photo:[],
			IDnum:'',
			IDname:'',
			relation:'',
			isScan:true,
			relation:'',
			relationType:'place'
		};
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey,
			cardType:state=>state.cardType
		})
	},
	methods:{
		getLostRelateType(type){
			this.relationType=type;
		},
		postPhone(){
			let that=this;
			that.isScan=true;
			if(!this.sessionKey) return uni.showToast({
				title:'请先授权',
				icon:'none'
			})
			uni.chooseImage({
				count:1,
				sizeType:'compressed',
				success:(res)=>{
					that.photo=res.tempFilePaths;
					this.$api.pubCor(that.photo[0],{
						sessionKey:that.sessionKey,
						type:that.cardType	
					}).then(res=>{
						let data=JSON.parse(res).words_result
						this.IDnum=data['公民身份号码'].words;
						this.IDname=data['姓名'].words;
						that.isScan=false;
					})
				}
			})
		},
		deletePhoto(){
			this.photo=[];
		},
		formSubmit(e){
			let that=this;
			if(!that.sessionKey){
			uni.showToast({
				title: '请先完成授权!',
				icon:'none'
			});
				return false;
			}
			if(that.IDname===''||that.IDnum===''){
				return uni.showToast({
					title:'请录入信息!',
					icon:'none'
				})
			}
			if(that.relation===''||that.relationType===''){
				return uni.showToast({
					title:'请填写联系方式',
					icon:'none'
				})
				return false;
			}
			if(that.relationType==='tel'){
				if(!(/^1[3456789]\d{9}/.test(that.relation))){
					uni.showToast({
						title:'手机号有误,请重填!',
						icon:'none'
					})
					console.log(that.relation)
					return false;
				}
			}
			this.$api.pubCard({
				sessionKey:that.sessionKey,
				cardType:that.cardType,
				cardNum:that.IDnum,
				cardName:that.IDname,
				relation:that.relationType+':'+that.relation
			}).then(res=>{
				console.log(res)
				if(res.code===1){
					uni.showToast({
						title:res.msg
					})
					that.IDnum='';
					that.IDname='';
					that.relation='';
					that.photo=[];
				}
			})
		},
		getPreview(){
			uni.previewImage({
				current:0,
				urls:this.photo
			})
		}
	}
}
